.second-reason {
  display: flex;
  flex-direction: column;
}
.second-reason .second-reason__descrip {
  display: flex;
  width: 320px;
}
.second-reason .second-reason__descrip--blue {
  background-color: #81b3d2;
}
.second-reason .second-reason__descrip-block {
  color: #ffffff;
  width: 282px;
  margin: auto;
  text-align: center;
  padding-top: 54px;
  padding-bottom: 40px;
}
.second-reason .second-reason__title {
  margin-bottom: 11px;
}
.second-reason .second-reason__num {
  margin-bottom: 17px;
}
.second-reason .second-reason__image {
  display: flex;
}
@media (min-width: 768px) {
  .second-reason {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .second-reason .second-reason__image {
    display: flex;
  }
  .second-reason .second-reason__descrip-block {
    color: #ffffff;
    width: 184px;
    margin: auto;
    text-align: center;
    padding-top: 43px;
    padding-bottom: 41px;
  }
  .second-reason .second-reason__descrip {
    width: 256px;
  }
}
@media (min-width: 1280px) {
  .second-reason {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .second-reason .second-reason__descrip-block {
    color: #ffffff;
    width: 235px;
    margin: auto;
    text-align: center;
    padding-top: 43px;
    padding-bottom: 52px;
  }
  .second-reason .second-reason__descrip {
    width: 400px;
  }
}
